import { useRef } from "react";
import { useBioReveal } from "../hooks/gsap";

const Bio = () => {
  const bioRef = useRef(null);

  useBioReveal(bioRef, 2);

  return (
    <div className='bio mt-20 container mx-auto overflow-hidden'>
      <p ref={bioRef}>
        Soy un apasionado desarrollador y diseador de videojuegos, especializado en la creacin de experiencias interactivas a travs de arte 2D, 3D y programacin. Con una trayectoria enfocada en proyectos innovadores, busco constantemente nuevas herramientas y tecnologas para mejorar mis habilidades. Mi objetivo es resolver problemas complejos con creatividad y lgica, y contribuir al avance de la industria del entretenimiento digital.
      </p>
    </div>
  );
};

export default Bio;
